// ColorManager.js
var ColorManager = pc.createScript('colorManager');

ColorManager.attributes.add('model', { type: 'entity' });
ColorManager.attributes.add('materials', { type: 'asset', assetType: 'material', array: true });

ColorManager.prototype.initialize = function () {
    this.app.on('ColorManager:Red', function () {
        this.changeMat(2);
    }, this);
    this.app.on('ColorManager:Blue', function () {
        this.changeMat(1);
    }, this);

    this.app.on('ColorManager:Green', function () {
        this.changeMat(4);
    }, this);

    this.app.on('ColorManager:Yellow', function () {
        this.changeMat(3);
    }, this);

    this.app.on('ColorManager:Pink', function () {
        this.changeMat(0);
    }, this);
};

// update code called every frame
ColorManager.prototype.changeMat = function (id) {
    if (this.model && this.model.render) {
        var material = this.model.render.meshInstances[0].material;
        if (material) {
            this.model.render.meshInstances[0].material = this.materials[id].resource;
            material.update();
        }
    }
};


