// ui.js
var Ui = pc.createScript('ui');

Ui.attributes.add('css', { type: 'asset', assetType: 'css', title: 'CSS Asset' });
Ui.attributes.add('html', { type: 'asset', assetType: 'html', title: 'HTML Asset' });
Ui.attributes.add('header', { type: 'asset', assetType: 'html', title: 'header Asset' });

Ui.attributes.add('bodyPart', { type: 'entity' });
Ui.attributes.add('bumperPart', { type: 'entity' });
Ui.attributes.add('seatPart', { type: 'entity' });
Ui.attributes.add('handlePart', { type: 'entity' });
Ui.prototype.initialize = function () {
    // create STYLE element
    var style = document.createElement('style');

    // append to head
    document.head.appendChild(style);
    style.innerHTML = this.css.resource || '';
    // console.log(style.innerHTML);
    var bootstrapCss = document.createElement('link');
    var bootstrapJs = document.createElement('script');
    bootstrapCss.rel = 'stylesheet';
    bootstrapCss.href = 'https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/css/bootstrap.min.css';
    bootstrapJs.src = 'https://cdn.jsdelivr.net/npm/bootstrap@5.1.3/dist/js/bootstrap.bundle.min.js';
    document.head.appendChild(bootstrapCss);
    document.head.appendChild(bootstrapJs);
    // Add the HTML
    this.div = document.createElement('div');
    this.div.classList.add('wrapper');
    this.navbar = document.createElement('div');
    this.navbar.classList.add('navbar');
    this.div.innerHTML = this.html.resource || '';
    this.navbar.innerHTML = this.header.resource || '';
    // console.log(this.div.innerHTML);

    // append to body
    // can be appended somewhere else

    document.body.appendChild(this.div);
    document.body.appendChild(this.navbar);

    this.bindEvents();

    if (window.innerWidth <= 1010) {
        document.getElementById('accordionExample').style.display = 'none';
        document.getElementById('mobileView').style.display = 'block';
    } else {
        this.app.mouse.on('mousedown', (e) => {
            if (e.element.id == 'application-canvas') {
                pc.app.root.findByName('Camera').script.orbitCamera.enabled = true;
            } else {
                pc.app.root.findByName('Camera').script.orbitCamera.enabled = false;
            }
        }, this);
        document.getElementById('accordionExample').style.display = 'block';
        document.getElementById('mobileView').style.display = 'none';
    }
};
Ui.prototype.update = function () {
    if (window.innerWidth <= 1010) {
        document.getElementById('accordionExample').style.display = 'none';
        document.getElementById('mobileView').style.display = 'block';
    } else {
        document.getElementById('accordionExample').style.display = 'block';
        document.getElementById('mobileView').style.display = 'none';
    }
};
Ui.prototype.bindEvents = function () {

    var self = this;
    // example
    //
    // get button element by class
    var activeAccordion = '';
    this.selectedPart;
    var buttons = document.getElementsByClassName("accordion-button");
    var clickedColor = document.getElementsByClassName("color-select-item_color");
    var resetToOrigin = document.getElementById("resetToOrigin");
    var clickedTab = document.getElementsByClassName("tabButton");
    resetToOrigin.addEventListener('click', function (e) {
        e.preventDefault();
        pc.app.fire('origin');
        activeAccordion = 'Origin';
    });
    if (clickedColor.length > 0) {
        for (var i = 0; i < clickedColor.length; ++i) {
            clickedColor[i].addEventListener('click', (e) => {
                var selectedColor = e.target.style.backgroundColor.replace(/[^\d,]/g, '').split(',');
                var material;
                if (activeAccordion == 'Body') {
                    material = this.bodyPart.render.meshInstances[0].material;
                    material.diffuse.set(selectedColor[0] / 255, selectedColor[1] / 255, selectedColor[2] / 255);
                    material.update();
                } else if (activeAccordion == 'Bumper') {
                    material = this.bumperPart.render.meshInstances[0].material;
                    material.diffuse.set(selectedColor[0] / 255, selectedColor[1] / 255, selectedColor[2] / 255);
                    material.update();
                } else if (activeAccordion == 'Seat') {
                    var selectedTexture = e.target.nextElementSibling.innerText.replace(/ /g, "-").toLowerCase();
                    var fullString = 'seat_' + selectedTexture + '.jpg';
                    this.seatPart.render.material.diffuseMap = pc.app.assets.find(fullString).resource;
                    this.seatPart.render.material.update();
                    console.log(this.seatPart.render.material.diffuseMap);
                } else if (activeAccordion == 'Handle') {
                    material = this.handlePart.render.meshInstances[0].material;
                    material.diffuse.set(selectedColor[0] / 255, selectedColor[1] / 255, selectedColor[2] / 255);
                    material.update();
                    console.log(this.handlePart.render.meshInstances[0].material);
                }
            });
        }
    }
    Array.prototype.forEach.call(document.getElementsByClassName("tabButton"), (child) => {
        activeAccordion = 'Body';
        child.addEventListener('click', (e) => {
            console.log(e.target.innerText);
            if (e.target.innerText == 'Bumper') {
                activeAccordion = 'Bumper';
                pc.app.fire('head');
            } else if (e.target.innerText == 'Seat') {
                activeAccordion = 'Seat';
                pc.app.fire('top');
            } else if (e.target.innerText == 'Body') {
                activeAccordion = 'Body';
                pc.app.fire('side');
            } else if (e.target.innerText == 'Handle') {
                activeAccordion = 'Handle';
                pc.app.fire('handle');
            }
            for (var i = 0; i < clickedTab.length; ++i) {
                if (e.target == clickedTab[i]) {
                    child.classList.add('mobileActive');
                } else {
                    clickedTab[i].classList.remove('mobileActive');
                }

            }
        });
    });
    // if found
    if (buttons.length > 0) {
        for (var i = 0; i < buttons.length; ++i) {
            buttons[i].addEventListener('click', function (e) {
                pc.app.root.findByName('Camera').script.orbitCamera.enabled = true;
                const siblings = e.target.closest(".accordion").querySelectorAll(".accordionButton");
                e.target.classList.toggle("active");
                Array.prototype.forEach.call(siblings, child => {
                    if (child != e.target) {
                        child.nextElementSibling.style.maxHeight = null;
                    } else {
                        e.target.nextElementSibling.style.maxHeight = child.nextElementSibling.scrollHeight + "px";
                    }
                });
                if (e.target.innerText == 'Bumper') {
                    activeAccordion = 'Bumper';
                    pc.app.fire('head');
                } else if (e.target.innerText == 'Seat') {
                    activeAccordion = 'Seat';
                    pc.app.fire('orbit:top');
                    pc.app.fire('top');
                } else if (e.target.innerText == 'Body') {
                    activeAccordion = 'Body';
                    pc.app.fire('side');
                } else if (e.target.innerText == 'Handle') {
                    activeAccordion = 'Handle';
                    pc.app.fire('handle');
                }
            }, false);
        }
    }

};

