// FocusePoint.js
var FocusePoint = pc.createScript('focusePoint');

FocusePoint.attributes.add("eventName", { type: 'string' });
FocusePoint.attributes.add("targetPoint", { type: 'entity' });

FocusePoint.prototype.initialize = function () {
    this.app.on(this.eventName, function () {
        this.app.fire('orbitCam:lookThisHotspot', this.entity.getPosition(), this.targetPoint.getPosition());
    }, this);
};

FocusePoint.prototype.update = function (dt) {

};



