// camera-controller.js
var CameraController = pc.createScript("cameraController");


Object.defineProperty(CameraController.prototype, "distance", {
  get: function () {
    return this.orbitCamera.distance;
  },

  set: function (value) {
    this.orbitCamera.distance = value;
    this.secsSinceUserInput = 0;
  }
});

Object.defineProperty(CameraController.prototype, "pitch", {
  get: function () {
    return this.orbitCamera.pitch;
  },

  set: function (value) {
    this.orbitCamera.pitch = value;
    this.secsSinceUserInput = 0;
  }
});

// Property to get and set the yaw of the camera around the pivot point (degrees)
Object.defineProperty(CameraController.prototype, "yaw", {
  get: function () {
    return this.orbitCamera.yaw;
  },

  set: function (value) {
    this.orbitCamera.yaw = value;
    this.secsSinceUserInput = 0;
  }
});

CameraController.prototype.initialize = function () {
  this.orbitCamera = this.entity.script.orbitCamera;
  this.app.on('cameraController:resetUserInputSession', function () {
    this.secsSinceUserInput = 0;
  }, this);
  this.animateOnload = 0;
};

CameraController.prototype.update = function (dt) {
  if (window.innerWidth <= 1010) {
    this.entity.camera.rect.z = 1;
  } else {
    this.entity.camera.rect.z = 0.8;
  }
  this.app.on('handle', () => { this.secsSinceUserInput = 0; }, this);
  this.app.on('side', () => { this.secsSinceUserInput = 0; }, this);
  this.app.on('top', () => { this.secsSinceUserInput = 0; }, this);
  this.app.on('head', () => { this.secsSinceUserInput = 0; }, this);
  this.app.on('origin', () => { this.secsSinceUserInput = 0; }, this);
  this.secsSinceUserInput += dt;
  this.secsIdleAutoOrbit = 10;
  if (this.secsSinceUserInput >= this.secsIdleAutoOrbit) {
    if (!this.orbiting) {
      this.app.root.findByName('Camera').script.orbitCamera.enabled = true;
      this.app.fire('origin');
    }
  }
};


CameraController.prototype.postInitialize = function () {
  if (this.orbitCamera) {
    this.startDistance = this.orbitCamera.distance;
    this.startYaw = this.orbitCamera.yaw;
    this.startPitch = this.orbitCamera.pitch;
    this.startPivotPosition = this.orbitCamera.pivotPoint.clone();
  }
};

